/*
 *  Designers/collaborations CSS Carousel
 *    Scripts for switching between tabs and dragging slider
 *
 *  Version:
 *    1.0.0 - 2021/11/22
 */

import { initMouseDragging } from '../../tools/utilities/carousel-tools';
import {
  CLASS_INITIALISED, CLASS_VISIBLE, CLASS_ACTIVE
} from './../../tools/constants/classes';

// Selectors
const SELECTOR_CONTAINER = '[data-designers-carousel]';
const SELECTOR_DESIGNERS_TAB = '[data-tab-designers]';
const SELECTOR_COLLABS_TAB = '[data-tab-collabs]';
const SELECTOR_DESIGNERS_SLIDER = '[data-slider-designers]';
const SELECTOR_COLLABS_SLIDER = '[data-slider-collabs]';
const SELECTOR_TAB_BLOB = '[data-active-tab-blob]';

const CLASS_FIRST_TAB_ACTIVE = 'is-first-active';
const CLASS_SECOND_TAB_ACTIVE = 'is-second-active';

document.querySelectorAll<HTMLElement>(SELECTOR_CONTAINER).forEach(container => {
  // Check if section has already been initialized
  if (container.classList.contains(CLASS_INITIALISED)) return;
  container.classList.add(CLASS_INITIALISED);

  const designersTab = container.querySelector<HTMLElement>(SELECTOR_DESIGNERS_TAB);
  const collabsTab = container.querySelector<HTMLElement>(SELECTOR_COLLABS_TAB);
  const designersSlider = container.querySelector<HTMLElement>(SELECTOR_DESIGNERS_SLIDER);
  const collabsSlider = container.querySelector<HTMLElement>(SELECTOR_COLLABS_SLIDER);
  const tabBlob = container.querySelector<HTMLElement>(SELECTOR_TAB_BLOB);

  if (!designersTab || !collabsTab || !designersSlider || !collabsSlider || !tabBlob) return;

  /**
   * Add active classes to first tab and slider
   * Remove active classes from second tab and slider
   * Move active tab blob behind the selected tab
   */
  designersTab.addEventListener('click', () => {
    designersTab.classList.add(CLASS_ACTIVE);
    collabsTab.classList.remove(CLASS_ACTIVE);
    designersSlider.classList.add(CLASS_VISIBLE);
    collabsSlider.classList.remove(CLASS_VISIBLE);
    tabBlob.classList.add(CLASS_FIRST_TAB_ACTIVE);
    tabBlob.classList.remove(CLASS_SECOND_TAB_ACTIVE);
  });

  /**
 * Add active classes to second tab and slider
 * Remove active classes from first tab and slider
 * Move active tab blob behind the selected tab
 */
  collabsTab.addEventListener('click', () => {
    designersTab.classList.remove(CLASS_ACTIVE);
    collabsTab.classList.add(CLASS_ACTIVE);
    designersSlider.classList.remove(CLASS_VISIBLE);
    collabsSlider.classList.add(CLASS_VISIBLE);
    tabBlob.classList.remove(CLASS_FIRST_TAB_ACTIVE);
    tabBlob.classList.add(CLASS_SECOND_TAB_ACTIVE);
  });


  // Mouse drag functionality
  [designersSlider, collabsSlider].forEach(slider => {
    // Initially center the slider
    slider.scrollTo({left: (slider.scrollWidth - slider.clientWidth)/2});

    // Allow for mouse dragging
    initMouseDragging(slider);
  });
});